// width
@mixin r($width) {
  @media only screen and (max-width: $width+ "px") {
    @content; } }

@mixin rmin($width) {
  @media only screen and (min-width: $width+ "px") {
    @content; } }

// height
@mixin rh($height) {
  @media only screen and (max-height: $height + "px") {
    @content; } }

@mixin rhmin($height) {
  @media only screen and (min-height: $height + "px") {
    @content; } }




// fonts
@mixin font {
  font-family: 'roboto', Arial, sans-serif;
  color: #7360F2; }







// flex in center
@mixin center {
  display: flex;
  justify-content: center;
  align-items: center; }
@mixin center-x {
  display: flex;
  justify-content: center; }
@mixin center-y {
  display: flex;
  align-items: center; }
@mixin center-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
